import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex);
var store = new vuex.Store({ //store对象
    state: {
        lang: "zh",
    },
    mutations: {
        UPDATE_LANG(state, lang) {
            state.lang = lang;
        },
    }
})

export default store


