import Yas from "eosjs-classic-yas";
import createHash from "create-hash";

//chain const
const httpEndpoint = "https://rpc.yas.plus";
const host = "rpc.yas.plus";
const chainId =
    "ed8636abfe625d99fc9a759d49a016fd8dcae9193676a020aae2540c9fffe32f";

const httpNetwork = {
    host: host,
    blockchain: "eos",
    chainId: chainId,
    port: 443,
    protocol: "https"
};

const eosOptions = {
    broadcast: true,
    sign: true,
    chainId: chainId
};

const txOptions = {
    broadcast: true,
    sign: true
};

const pluginName = "scatter";

//private
const getF = "2d85addc58190daeee657f751a75c26";
const getL = "66c410523bf89d616c8bf798e306fe719";

//contract info
const contractName = "redredredred";
const actor = "getredgetred";
const permission = "active";

var ecc = Yas.modules.ecc;

var yas = Yas({
    httpEndpoint: httpEndpoint,
    chainId: chainId,
    keyProvider: ecc.PrivateKey.fromHex(getF + getL).toString()
});

export default {
    //获取红包信息
    getRecord: function (id) {
        return yas
            .getTableRows({
                scope: contractName,
                code: contractName,
                table: "envelopes",
                lower_bound: id,
                json: true
            })
            .then(result => {
                let row = result.rows[0];
                if (row && id == row.envelope_id) {
                    //console.log(row);
                    return row;
                } else {
                    return Promise.reject('RedNoExitMessage');
                }
            });
    },
    //生成密钥对
    generateKeyPair: async function () {
        //get keypair
        let privateKey = await ecc.randomKey();
        //console.log("privateKey:", privateKey);
        let publicKey = ecc.privateToPublic(privateKey);
        //console.log("publicKey:", publicKey);
        return { privateKey: privateKey, publicKey: publicKey };
    },
    //前面交易数据
    sign: function (id, account, time, f, l) {
        let rawData = account + id + time;
        //console.log(rawData);

        let privateKey = ecc.PrivateKey.fromHex(f + l).toString();

        let hexData = createHash("sha256")
            .update(rawData)
            .digest("hex");
        console.log(hexData);

        let sigData = ecc.Signature.signHash(hexData, privateKey).toString();

        console.log(sigData);

        let sig = ecc.Signature.from(sigData);

        return sig;
    },
    //用红包创建账号
    newAccount: function (id, account, sig, publicKey) {
        let tx_data = {
            actions: [{
                account: contractName,
                name: "newaccount",
                authorization: [{ actor: actor, permission: permission }],
                data: {
                    envelope_id: id,
                    user: account,
                    sig: sig,
                    public_key_str: publicKey
                }
            }]
        };

        return yas.transaction(tx_data);
    },
    //拆分私钥
    cutKey: function (privateKey) {
        let hexStr = ecc
            .PrivateKey(privateKey)
            .toBuffer()
            .toString("hex");
        let hexLen = hexStr.length / 2;
        let f = hexStr.slice(0, hexLen);
        let l = hexStr.slice(hexLen);
        return {
            f: f,
            l: l
        };
    },
    //连接插件
    connectIdentity() {
        return new Promise((resolve, reject) => {
            if (window[pluginName]) {
                window[pluginName]
                    .getIdentity({ accounts: [httpNetwork] })
                    .then(identity => {
                        let account = identity.accounts[0];
                        //this.accountName = account_name;
                        resolve(account);
                    })
                    .catch(error => {
                        console.error(error);
                        reject(error);
                    });
            } else {
                setTimeout(() => {
                    this.connectIdentity()
                        .then(account => {
                            resolve(account);
                        })
                        .catch(error => {
                            reject(error);
                        });
                }, 2000);
            }
        });
    },
    //切换插件用户
    changeIdentity() {
        return new Promise((resolve, reject) => {
            if (window[pluginName]) {
                return window[pluginName].forgetIdentity().then(() => {
                    resolve();
                });
            } else {
                reject("ironman not loaded!");
            }
        });
    },
    //发送红包
    sendEnvelope(account, asset, memo) {
        let plugin = window[pluginName].eos(httpNetwork, Yas, eosOptions, "https");
        let tx_data = {
            actions: [{
                account: "eosio.token",
                name: "transfer",
                authorization: [{ actor: account.name, permission: account.authority }],
                data: { from: account.name, to: contractName, quantity: asset, memo: memo }
            }]
        };
        return plugin.transaction(tx_data);
        //return plugin.transfer(accountName, contractName, asset, memo);
    },
    //检查账号是否存在
    async checkAccountExist(accountName) {
        try {
            let res = await yas.getAccount(accountName);
            //console.log(res);
            if (res.account_name == accountName) {
                return true;
            } else {
                return false;
            }
        } catch (err) {
            //console.log(err);
            return false;
        }
    },
    //领取红包
    getEnvelope(id, account, sig) {
        let tx_data = {
            actions: [{
                account: contractName,
                name: "get",
                authorization: [{ actor: actor, permission: permission }],
                data: { envelope_id: id, user: account, sig: sig }
            }]
        };

        return yas.transaction(tx_data);
    }
};


