<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: "App",
  created() {
    if (localStorage.getItem("Lang")) {
      this.$i18n.locale = localStorage.getItem("Lang");
    } else {
      localStorage.setItem("Lang", this.$i18n.locale);
    }
  }
};
</script>

<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;
#app {
  margin: 0 auto;
  max-width: 375 * @rem;
}
</style>



