import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'home',
        component: resolve => require(['@/components/home.vue'], resolve)
    }, {
        path: '/red',
        name: 'red',
        component: resolve => require(['@/components/red.vue'], resolve)
    }, {
        path: '/redlist',
        name: 'redlist',
        component: resolve => require(['@/components/redlist.vue'], resolve)
    }, {
        path: '/account',
        name: 'account',
        component: resolve => require(['@/components/account.vue'], resolve)
    }, {
        path: '/about',
        name: 'about',
        component: resolve => require(['@/components/about.vue'], resolve)
    }, {
        path: '/redabout',
        name: 'redabout',
        component: resolve => require(['@/components/redabout.vue'], resolve)
    }, {
        path: '/receive',
        name: 'receive',
        component: resolve => require(['@/components/receive.vue'], resolve)
    }, {
        path: '/detail',
        name: 'detail',
        component: resolve => require(['@/components/detail.vue'], resolve)
    }]
})


